import React ,{useState} from 'react';

const Textform = (props) => {

    const [text, setText] = useState("");

    const handleOnChange =  (e)=> {
        setText(e.target.value);
    }

    const convertToUppercase = ()=>{
        if(text === "") {
            props.showAlert("Enter something to proceed", "warning");
        } else {
            setText(text.toUpperCase());
            props.showAlert("Converted to uppercase", "success");
        }
        
    }

    const convertToLowercase = ()=>{
        if(text === "") {
            props.showAlert("Enter something to proceed", "warning");
        } else {
            setText(text.toLowerCase());
            props.showAlert("Converted to lowercase", "success");
        }
    }

    const copyText = ()=>{
        if(text === "") {
            props.showAlert("Enter something to proceed", "warning");
        } else {
            navigator.clipboard.writeText(text);
            props.showAlert("Copied to clipboard", "success");
        }
    }

    return (
    <div className="container my-5">
        <h3>ENTER YOUR TEXT</h3>
        <div className="my-3">
            <textarea className="form-control" onChange={handleOnChange} value={text} rows="8"></textarea>
        </div>
        <button type="button" onClick={convertToUppercase} className="btn btn-primary mx-2">Uppercase</button>
        <button type="button" onClick={convertToLowercase} className="btn btn-primary mx-2">Lowercase</button>
        <button type="button" onClick={copyText} className="btn btn-primary mx-2">Copy Text</button>
    </div>
    );
}

export default Textform;
